import interfaceurl from '@/utils/interface.js'
import authPage from '@/components/authorization-page.vue' //引入授权窗体
export default {
	components: {
		authPage
	},
	data() {
		return {
			detail: {},
			userInfo: {}
		}
	},
	onLoad() {
		
	},
	onShow() {
		if(this.userInfo) {
			this.getUserInfo()
		}
	},
	methods: {
		getUserInfo() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.showDetail, {}, false).then((res) => {
				that.userInfo = res.data
			});
		}
	}
}
